import React,{useState} from 'react'
import { Link,Navigate } from 'react-router-dom'
import Axios from 'axios';
import './login.css'
// import {useEffect} from 'react';
function Login() {

  // const [userFnamereg, setUserFnamereg] = useState("");
  // const [userLnamereg, setUserLnamereg] = useState("");
  const [userEmail, setuserEmail] = useState("");
  const [userpass, setUserpass] = useState("");
  const [errorMessage,seterrorMessage]=useState("");
  // Axios.defaults.withCredentials=true;
  const Login = (e) => {
    e.preventDefault();
    Axios.post("http://localhost:5000/user/login", {
      // First_Name: userFnamereg,
      // Second_Name: userLnamereg,
      Email: userEmail,
      password: userpass,
    }).then((response) => {
    if(response.LoggedIn){
      <Navigate to="/upload"/>
      // localStorage.setItem("LoggedIn",true);
      // localStorage.setItem("Email",response.data.Email);
      // seterrorMessage(response.data.message);

    }
    });
  };

  // useEffect(() => {
  //   Axios.get("http://localhost:5000/user/login").then((result)=>{
  //     console.log(result);
  //   })
  // }, [])
    return (
      <>
      <form className='form'>
      <h3>Sign In</h3>
        <div className="mb-3">
          <label>Email address</label>
          <input
            type="email"
            className="form-control"
            placeholder="Enter email"
            onChange={(e) => {
              setuserEmail(e.target.value);
            }}
          />
        </div>
        <div className="mb-3">
          <label>Password</label>
          <input
            type="password"
            className="form-control"
            placeholder="Enter password"
            onChange={(e) => {
              setUserpass(e.target.value);
            }}
          />
        </div>
        <div className="mb-3">
        <div className="custom-control custom-checkbox">
        <input
              type="checkbox"
              className="custom-control-input"
              id="customCheck1"
              />
            <label className="custom-control-label" htmlFor="customCheck1">
              Remember me
            </label>
            </div>
        </div>
        <div className="d-grid">
          <button type="submit" className="btn btn-primary" onClick={Login}>
          Submit
          </button>
        </div>
        <p className="forgot-password text-right">
           Not have account?<Link to="/signup"> Signup</Link>
           <h3 className='h3'>{errorMessage}</h3>
        </p>
      </form>
      </>
    )
  };

  export default Login;