import React from "react";

import { Container, Row, Col } from "reactstrap";

import CommonSection from "../components/UI/common-section/CommonSection";

import NftCard from "../components/UI/Nft-card/Nftcard";
import img from "../assets/images/img-01.jpg";
import ava from "../assets/images/ava-02.png";


import "../styles/createitem.css"
// import SignUp from '../components/loginsignup/SignUp';
// <SignUp/>

const item = {
  id: "05",
  title: "Travel Monkey Club",
  count: "0",
  desc: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Veniam adipisci cupiditate officia, nostrum et deleniti vero corrupti facilis minima laborum nesciunt nulla error natus saepe illum quasi ratione suscipit tempore dolores. Recusandae, similique modi voluptates dolore repellat eum earum sint.",
  imgUrl: img,
  creator: "Alishba Naeem",
  creatorImg: ava,
  currentBid: 4.89,
};

const Create = () => {
  return (
    <>
      <CommonSection title="Create Item" />

      <section>
        <Container>
          <Row>
            <Col lg="3" mg="4" sm="6">
              <h5 className="mb-4 text-light">Preview Item</h5>
              <NftCard item={item} />
            </Col>
            <Col lg="9" mg="8" sm="6">
              <div className="create__item">
                <form>
                  <div className="form__input">
                    <label for="">Upload File</label>
                    <input
                      type="file"
                      placeholder="Browse"
                      className="upload__input"
                    />
                  </div>
                  <div className="form__input">
                    <label for="">Price</label>
                    <input
                      type="number"
                      placeholder="Enter Amount for one Item (ETH) "
                    />
                  </div>
                  <div className="form__input">
                    <label for="">Minimum Bid</label>
                    <input type="number" placeholder="Enter minimum bid" />
                  </div>

                  <div className="d-flex align-items-center justify-content-between">
                    <div className="form__input w-50">
                      <label for="">Starting Date</label>
                      <input type="date" placeholder="Enter minimum bid" />
                    </div>
                    <div className="form__input w-50">
                      <label for="">Expiration Date</label>
                      <input type="date" placeholder="Enter minimum bid" />
                    </div>
                  </div>

                  <div className="form__input">
                    <label for="">Title</label>
                    <input type="text" placeholder="Enter title" />
                  </div>
                  <div className="form__input">
                    <label for="">Description</label>
                    <textarea rows="7"  placeholder="Enter Description" className="w-100"></textarea>
                  </div>
                </form>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
    </>
  );
};

export default Create;
