import React,{useState} from "react";

import CommonSection from "../components/UI/common-section/CommonSection";
import { Container, Row, Col } from "reactstrap";

import NFTCard from "../../src/components/UI/Nft-card/Nftcard"

import '../styles/market.css'

import {NFT__DATA} from "../assets/data/data.js"
const Market = () => {


  const [Data,setData]=useState(NFT__DATA)

const handleCategory=()=>{

}
const handleItems=()=>{

}


//============ SORTING DATA BY HIGH,MID,LOW
const handlesort=(e)=>{


  const filterValue=e.target.value;
  if(filterValue==="high"){
    const filterData=NFT__DATA.filter(item=>item.currentBid>6)

    setData(filterData);
  }
  else if(filterValue==="mid"){
    const filterData=NFT__DATA.filter(item=>item.currentBid>5.5 && item.currentBid<6 )

    setData(filterData);
  }
  else if(filterValue==="low"){
    const filterData=NFT__DATA.filter(item=>item.currentBid>4.89&&item.currentBid<5.5)

    setData(filterData);
  }
  else if(filterValue==="sortby"){
    const filterData=NFT__DATA.filter(item=>item.currentBid>0)

    setData(filterData);
  }

}
  return (
    <>
      <CommonSection title={"MarketPlace"} />

      <section>
        <Container>
          <Row>
            <Col lg="12" className="mb-5">
              <div className="market__product__filter d-flex align-items-center justify-content-between">
                <div className="filter__left d-flex align-items-center gap-5">
                  <div className="all__category__filter">
            
                    <select onChange={handleCategory}>
                    <option>All Categories</option>
                      <option value="art">Art</option>
                      <option value="music">Music</option>
                      <option value="domain-name">Domain Name</option>
                      <option value="virtual-world">Virtual World</option>
                      <option value="trending-card">Trending Cards</option>
                    </select>
                  </div>
                </div>

                <div className="all__Items__filter">
                  <select onChange={handleItems}>
                  <option>All Items</option>
                    <option value="single-item">Single Item</option>
                    <option value="bundle">Bundle</option>
                  </select>
                </div>
                <div className="filter__right">
                
                <select onChange={handlesort}>
                <option value="sortby">Sort By</option>
                <option value="high">High Rate</option>
                <option value="mid">Mid Rate</option>
                <option value="low">Low Rate</option>
                </select>
                </div>
              </div>
            </Col>

            {
              Data.map(item=>(
                <Col lg="3" md="4" sm="6" className="mb-4" key={item.id}>
                <NFTCard item={item}/>
                </Col>
              ))
            }
          </Row>
        </Container>
      </section>
    </>
  );
};

export default Market;
