import React from "react";
import "./Modal.css";

const Modal = ({ setModal }) => {
  return (
    <>
      <div className="modal__wrapper">
        <div className="single__modal">
          <span className="close__modal">
            <i className="ri-close-line" onClick={()=>setModal(false)}></i>
          </span>
          <h6 className="text-center text-light">Place a Bid</h6>
          <p className="text-center text-light">
            You must bid at least <span className="money">5.89ETH</span>
          </p>

          <div className="input__item mb-4">
            <input type="number" placeholder="00 : 00 ETH " />
          </div>
          <div className="input__item mb-3">
            <h6>Enter Quality,7 available</h6>
            <input type="number" placeholder="Enter Quality " />
          </div>

          <div className="d-flex align-items-center justify-content-between">
            <p>You must bid atleast</p>
            <span className="money">5.89 ETH</span>
          </div>
          <div className="d-flex align-items-center justify-content-between">
            <p>Service Fee</p>
            <span className="money">0.89 ETH</span>
          </div>
          <div className="d-flex align-items-center justify-content-between">
            <p>Total Bid Amount</p>
            <span className="money">5.89 ETH</span>
          </div>

          <button className="place__bid-btn">Place a Bid</button>
        </div>
      </div>
    </>
  );
};

export default Modal;
