import React from "react";
import CommonSection from "../components/UI/common-section/CommonSection";

import "../styles/wallet.css";
import { Container, Row, Col } from "reactstrap";

const wallet__data = [
  {
    title: "Bitcoin",
    desc: "Bitcoin is a decentralized digital currency that can be transferred on the peer-to-peer bitcoin network.You can easily sell and by NFTs using Bitcoin",
    icon: "ri-bit-coin-line",
  },
  {
    title: "CoinBase",
    desc: "CoinBase is a decentralized digital currency that can be transferred on the peer-to-peer bitcoin network.You can easily sell and by NFTs using CoinBase",
    icon: "ri-coin-line",
  },
  {
    title: "MetaMask",
    desc: "MetaMask is a decentralized digital currency that can be transferred on the peer-to-peer bitcoin network.You can easily sell and by NFTs using MetaMask",
    icon: "ri-money-cny-circle-line",
  },
  {
    title: "Autherem",
    desc: "Autherem is a decentralized digital currency that can be transferred on the peer-to-peer bitcoin network.You can easily sell and by NFTs using Autherem",
    icon: "ri-coins-line",
  },
];

const Wallet = () => {
  return (
    <>
      <CommonSection title="Connect Wallet" />

      <section>
        <Container>
          <Row>
            <Col lg="12" className="mb-5 text-center">
              <div className="w-50 m-auto">
                <h3 className="text-light">Connect Your Wallet</h3>

                <p>
                  connect your wallet and make money by selling your hidden
                  skills and make the world colorfull
                </p>
              </div>
            </Col>




            {
              wallet__data.map((item,index)=>(
                <Col lg="3" md="4" sm="6" key={index} className="mb-4">
                <div className="wallet__item">
                  <span>
                    <i className={item.icon}></i>
                  </span>
                  <h5>{item.title}</h5>
                  <p>{item.desc}</p>
                </div>
              </Col>
              ))
            }
            
          </Row>
        </Container>
      </section>
    </>
  );
};

export default Wallet;
